import React, { Component } from "react";
import productData from "../data/productData";
import CartRow from "./CartRow";

class ShoppingCart extends Component {
  constructor() {
    super();
    this.state = {
      data: productData,
    };
  }

  render() {
    const cartRow = this.state.data.map((product) =>
      product.inCart ? (
        <CartRow
          key={product.id}
          product={product}
          handleRemoveFromCart={this.props.handleRemoveFromCart}
          handleAddQty={this.props.handleAddQty}
          handleSubQty={this.props.handleSubQty}
        />
      ) : null
    );

    const subTotal = this.state.data.map((product) =>
      product.inCart ? Number((product.price * product.qty).toFixed(2)) : 0
    );

    const total = subTotal.reduce((a, b) => a + b, 0).toFixed(2);

    return (
      <div>
        <div className="overlay"></div>
        <div className="shopping-cart">
          <div className="order">
            <i
              className="far fa-times-circle"
              onClick={this.props.handleClose}
            ></i>
            <h1>my shopping cart</h1>
            <h2>You have {this.props.cartNum} items in your cart</h2>
            <div className="labels">
              <ul>
                <li>description</li>
                <li>quantity</li>
                <li>price</li>
              </ul>
            </div>
            {cartRow}
            <div className="total">
              <h2>total:</h2>
              <h6>${total}</h6>
            </div>
            <div className="decision">
              <button
                className="continue-shopping"
                onClick={this.props.handleClose}
              >
                continue shopping
              </button>
              <button className="checkout" onClick={this.props.handleCheckout}>
                checkout
              </button>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

export default ShoppingCart;
