import React, { Component } from "react";
import "./styles/styles.css";
import Header from "./components/Header";
import MainContent from "./components/MainContent";
import Footer from "./components/Footer";
import productData from "./data/productData";
import ShoppingCart from "./components/ShoppingCart";

class App extends Component {
  constructor() {
    super();
    this.state = {
      data: productData,
      cartNum: 0,
      success: false,
      fail: false,
      cartOpen: false,
      thankYou: false,
    };
    this.handleAddToCart = this.handleAddToCart.bind(this);
    this.handleRemoveFromCart = this.handleRemoveFromCart.bind(this);
    this.handleAddQty = this.handleAddQty.bind(this);
    this.handleSubQty = this.handleSubQty.bind(this);
    this.handleCheckout = this.handleCheckout.bind(this);
    this.handleOpen = this.handleOpen.bind(this);
    this.handleClose = this.handleClose.bind(this);
  }

  handleOpen() {
    this.setState({ cartOpen: true, success: false, fail: false });
  }

  handleClose() {
    this.setState({
      cartOpen: false,
      success: false,
      fail: false,
      thankYou: false,
    });
  }

  handleAddToCart(id) {
    let cartAdder = 0;
    let cartSuccess = false;
    let cartFail = false;
    const updatedData = this.state.data.map((product) => {
      if (product.id === id) {
        if (product.inCart === false) {
          product.inCart = true;
          cartAdder = 1;
          cartSuccess = true;
        } else cartFail = true;
      }
      return product;
    });
    this.setState((prevState) => ({
      data: updatedData,
      cartNum: prevState.cartNum + cartAdder,
      success: cartSuccess,
      fail: cartFail,
    }));
  }

  handleRemoveFromCart(id) {
    let cartRemover;
    const updatedData = this.state.data.map((product) => {
      if (product.id === id) {
        product.inCart = false;
        cartRemover = product.qty;
        product.qty = 1;
      }
      return product;
    });
    this.setState((prevState) => ({
      data: updatedData,
      cartNum: prevState.cartNum - cartRemover,
    }));
  }

  handleAddQty(id) {
    let cartAdder = 1;
    const updatedData = this.state.data.map((product) => {
      if (product.id === id) {
        if (product.qty >= 10) {
          cartAdder = 0;
          alert(
            "There is a limit of 10 " + product.title + " per cart. Thank you."
          );
        } else product.qty = product.qty + 1;
      }
      return product;
    });
    this.setState((prevState) => ({
      data: updatedData,
      cartNum: prevState.cartNum + cartAdder,
    }));
  }

  handleSubQty(id) {
    const updatedData = this.state.data.map((product) => {
      if (product.id === id) {
        if (product.qty <= 1) {
          product.inCart = false;
        } else product.qty = product.qty - 1;
      }
      return product;
    });
    this.setState((prevState) => ({
      data: updatedData,
      cartNum: prevState.cartNum - 1,
    }));
  }

  handleCheckout() {
    const checkoutData = this.state.data.map((product) => {
      if (product.inCart === true) {
        product.inCart = false;
        product.qty = 1;
      }
      return product;
    });
    this.setState({
      data: checkoutData,
      cartNum: 0,
      cartOpen: false,
      thankYou: true,
    });
  }

  render() {
    const successModal = this.state.success ? (
      <div className="success-modal">
        <div className="success">
          <div className="success-inner">
            <i className="far fa-times-circle" onClick={this.handleClose}></i>
            <h1>
              <i className="fas fa-check"></i>product added to your cart
            </h1>
            <div className="options">
              <button className="continue-shopping" onClick={this.handleClose}>
                continue shopping
              </button>
              <a href="#">
                <button className="view-cart" onClick={this.handleOpen}>
                  view cart
                </button>
              </a>
            </div>
          </div>
        </div>
        <div className="overlay"></div>
      </div>
    ) : null;

    const failModal = this.state.fail ? (
      <div className="fail-modal">
        <div className="fail">
          <div className="fail-inner">
            <i className="far fa-times-circle" onClick={this.handleClose}></i>
            <h1>
              <i className="fas fa-times"></i>product is already in your cart
            </h1>
            <div className="options">
              <button className="continue-shopping" onClick={this.handleClose}>
                continue shopping
              </button>
              <a href="#">
                <button className="view-cart" onClick={this.handleOpen}>
                  view cart
                </button>
              </a>
            </div>
          </div>
        </div>
        <div className="overlay"></div>
      </div>
    ) : null;

    const shoppingCart = this.state.cartOpen ? (
      <ShoppingCart
        cartNum={this.state.cartNum}
        handleClose={this.handleClose}
        handleRemoveFromCart={this.handleRemoveFromCart}
        handleAddQty={this.handleAddQty}
        handleSubQty={this.handleSubQty}
        handleCheckout={this.handleCheckout}
      />
    ) : null;

    const thankYouModal = this.state.thankYou ? (
      <div className="thankyou-modal">
        <div className="thankyou">
          <div className="thankyou-inner">
            <i className="far fa-times-circle" onClick={this.handleClose}></i>
            <h1>
              <i className="fas fa-check"></i>thank you for your purchase
            </h1>
            <div className="options">
              <button className="continue-shopping" onClick={this.handleClose}>
                continue shopping
              </button>
            </div>
          </div>
        </div>
        <div className="overlay"></div>
      </div>
    ) : null;

    return (
      <div>
        {successModal}
        {failModal}
        {shoppingCart}
        {thankYouModal}
        <Header
          cartNum={this.state.cartNum}
          handleRemoveFromCart={this.handleRemoveFromCart}
          handleAddQty={this.handleAddQty}
          handleSubQty={this.handleSubQty}
          handleCheckout={this.handleCheckout}
          handleOpen={this.handleOpen}
          handleClose={this.handleClose}
        />
        <MainContent handleAddToCart={this.handleAddToCart} />
        <Footer />
      </div>
    );
  }
}

export default App;
