import React from "react";

function CartRow(props) {
  return (
    <div className="cart-row">
      <div className="description">
        <img src={props.product.img} alt={props.product.title} />
        <div className="title">
          <h3>{props.product.title}</h3>
          <p>
            <i className="fas fa-long-arrow-alt-right"></i>1.55 oz
          </p>
          <h4 onClick={(event) => props.handleRemoveFromCart(props.product.id)}>
            remove
          </h4>
        </div>
      </div>
      <div className="quantity">
        <div
          className="subtract"
          onClick={(event) => props.handleSubQty(props.product.id)}
        >
          <i className="fas fa-chevron-down"></i>
        </div>
        <h5>{props.product.qty}</h5>
        <div
          className="add"
          onClick={(event) => props.handleAddQty(props.product.id)}
        >
          <i className="fas fa-chevron-up"></i>
        </div>
      </div>
      <div className="price">
        <h6>${(props.product.price * props.product.qty).toFixed(2)}</h6>
      </div>
    </div>
  );
}

export default CartRow;
