import React, { Component } from "react";
import productData from "../data/productData";
import Product from "./Product";

class MainContent extends Component {
  constructor() {
    super();
    this.state = {
      data: productData,
    };
  }

  render() {
    const products = this.state.data.map((product) => {
      return (
        <Product
          key={product.id}
          product={product}
          handleAddToCart={this.props.handleAddToCart}
        />
      );
    });

    return (
      <section>
        <div className="banner">
          <h1>chocolate</h1>
        </div>
        <div className="navigation-scheme">
          <div className="breadcrumbs">
            <i className="fas fa-home"></i>
            <i className="fas fa-chevron-right"></i>chocolate
          </div>
          <div className="sort">
            <p>best sellers</p>
            <i className="fas fa-chevron-down"></i>
          </div>
        </div>
        <div className="category">
          <div className="filter">
            <h1>Chocolate</h1>
            <ul>
              <li>Browse Chocolate</li>
              <li>Milk Chocolate</li>
              <li>Dark Chocolate</li>
              <li>White Chocolate</li>
              <li>Peanut Butter Filling</li>
              <li>Sponge Candy</li>
              <li>Caramel Filling</li>
              <li>Sea Salted</li>
              <li>S'More</li>
              <li>Coconut Filling</li>
            </ul>
          </div>
          <div className="products">{products}</div>
        </div>
      </section>
    );
  }
}

export default MainContent;
